body {
  margin: 0;
  background-color: rgb(255, 212, 220);
  background-image: url('Images/background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

* {
  color: rgb(209, 102, 120);
}

.app {
  margin-top: 10vh;
}

.header {
  margin-left: 6vw;
  margin-top: 30px;
  font-size: 80px;
  margin-bottom: 0px;
  text-decoration: underline;
}

.my-paragraph {
  margin-top: 10px;
  margin-left: 6vw;
  ;
  font-size: 18px;
  margin-bottom: 0px;
  font-weight: 500;
}

.my-paragraph-2 {
  margin-top: 20px;
  font-style: italic;
  font-size: 25px;
  margin-left: 6vw;
  ;
  color: rgb(255, 115, 138);
  margin-bottom: 20px;
  font-family: Great Vibes;
  text-decoration: underline;
}

.contact-button {
  font-weight: 600;
  background: none;
  border: 2px solid;
  padding: 10px;
  font-size: 18px;
  width: 150px;
  border-image-slice: 1;
  border-image-source: linear-gradient(to right, rgb(255, 125, 147), purple);
  margin-top: 10px;
  margin-left: 6vw;
  transition: all 0.5s;
}

.contact-button:hover {
  border-image-source: linear-gradient(to top, rgb(255, 125, 147), purple);
}

.contacts{
  display: flex;
}

.whatsapp-logo, .instagram-logo{
  margin-left: 6.5vw;
  margin-top: 40px;
  opacity: 80%;
  filter: invert(56%) sepia(18%) saturate(1522%) hue-rotate(314deg) brightness(84%) contrast(77%);
  height: 30px;
  transition: all 0.2s;
}

.whatsapp-logo:hover, .instagram-logo:hover{
  filter: invert(90%) sepia(18%) saturate(1522%) hue-rotate(314deg) brightness(84%) contrast(77%);
}

.instagram-logo{
  margin-left: 25px;
}

@media screen and (max-width: 1000px) {
  .header {
    font-size: 60px;
  }

  .my-paragraph {
    font-size: 14px;
  }

  .my-paragraph-2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 800px) {
  .header {
    font-size: 50px;
  }

  .my-paragraph {
    margin-top: 8px;
    font-size: 12px;
  }

  .my-paragraph-2 {
    margin-top: 10px;
    font-size: 18px;
  }

  .contact-button {
    margin-top: 0px;
  }
}